<template>
  <div>

    <el-header height="80px" :style="{ 'background-color': primaryColor }">
      <el-container class="logo">
        <img src="./assets/logo.svg"/>
        <a>Port Forward</a>
      </el-container>
    </el-header>

    <el-main>

      <el-row class="op-line">
        <span class="flow-stat">
          <i class="el-icon-upload2" style="color: #ff5b56"></i>
          发送：{{parseFlow(flow.send, 0)}}
        </span>
        <span class="flow-stat">
          <i class="el-icon-download" style="color: #00802f"></i>
          接收：{{parseFlow(flow.receive, 0)}}
        </span>
        <span class="flow-stat">
          <i class="el-icon-sort" style="color: #ffca2c; font-size: 18px;"></i>
          当前连接数/总连接数：{{flow.connect}}/{{flow.totalConnect}}
        </span>
        <el-button type="primary" icon="el-icon-plus" size="small" @click="dialogFormVisible = true" :loading="adding">添加</el-button>
      </el-row>

      <el-table :data="routes" style="width: 100%" highlight-current-row v-loading="routesLoading">
        <el-table-column label="#" width="80" type="index"></el-table-column>
        <el-table-column label="本地监听">
          <template slot-scope="scope">
            {{scope.row.host}}:{{scope.row.port}}
          </template>
        </el-table-column>
        <el-table-column label="上游服务器">
          <template slot-scope="scope">
            {{scope.row.targetHost}}:{{scope.row.targetPort}}
          </template>
        </el-table-column>
        <el-table-column label="发送" width="140">
          <template slot-scope="scope">
            {{parseFlow(scope.row.flow.send, 0)}}
          </template>
        </el-table-column>
        <el-table-column label="接收" width="140">
          <template slot-scope="scope">
            {{parseFlow(scope.row.flow.receive, 0)}}
          </template>
        </el-table-column>
        <el-table-column label="当前连接数" width="140" prop="flow.connect"></el-table-column>
        <el-table-column label="总连接数" width="140" prop="flow.totalConnect"></el-table-column>
        <el-table-column label="操作" width="140">
          <template slot-scope="scope">
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

    </el-main>


    <el-dialog title="添加路由映射" :visible.sync="dialogFormVisible" width="600px">
      <el-form :model="route" ref="addRoute" :rules="rules">
        <el-form-item label="本地host" prop="host" :label-width="formLabelWidth">
          <el-input v-model="route.host" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="本地端口" prop="port" :label-width="formLabelWidth">
          <el-input v-model="route.port" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="远程host" prop="targetHost" :label-width="formLabelWidth">
          <el-input v-model="route.targetHost" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="远程端口" prop="targetPort" :label-width="formLabelWidth">
          <el-input v-model="route.targetPort" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('addRoute')">确 定</el-button>
      </div>
    </el-dialog>


  </div>
</template>

<script>

  const service_prefix = "/api";

  const flow_unit = [
    'B', 'KB', 'MB', 'GB', 'TB', 'PB'
  ];

  const regx_ip = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/;
  const regx_host = /^(?=^.{3,255}$)[a-zA-Z0-9][-a-zA-Z0-9]{0,62}(\.[a-zA-Z0-9][-a-zA-Z0-9]{0,62})+$/;

  export default {
    name: 'app',
    components: {},
    data() {

      const validatePort = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('端口号不能为空'));
        } else if (!/^\d+$/.test(value)) {
          callback(new Error('端口号必须为数字'));
        } else if (value < 1 || value > 65535) {
          callback(new Error('端口号必须在1~65535之间'));
        } else {
          callback();
        }
      };


      const validateLocalHost = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('host不能为空'));
        } if (regx_ip.test(value)) {
          callback();
        } else {
          callback("本地host必须为本机IP地址");
        }
      };

      const validateHost = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('host不能为空'));
        } if (regx_ip.test(value)) {
          callback();
        } if (regx_host.test(value)) {
          callback();
        } else {
          callback("请输入正确的host");
        }
      };

      return {
        updating: false,
        adding: false,
        routesLoading: true,
        dialogTableVisible: false,
        dialogFormVisible: false,
        route: {
          host: '0.0.0.0',
          port: 8080,
          targetHost: '127.0.0.1',
          targetPort: 80
        },
        formLabelWidth: '80px',

        addVisible: false,
        primaryColor: '#409eff',
        flow: {},
        routes: [],

        rules: {
          port: [
            { validator: validatePort, trigger: 'blur' }
          ],
          host: [
            { validator: validateLocalHost, trigger: 'blur' }
          ],
          targetPort: [
            { validator: validatePort, trigger: 'blur' }
          ],
          targetHost: [
            { validator: validateHost, trigger: 'blur' }
          ]
        }
      }
    },

    created() {
      this.poll();
    },

    methods: {
      poll() {
        this.update();
        setTimeout(this.poll, 1000);
      },
      update() {
        this.updateState();
        this.updateList();
      },
      parseFlow(flow, idx) {
        while (flow > 1024) {
          flow /= 1024;
          idx++;
        }
        return flow.toFixed(2) + flow_unit[idx];
      },
      handleDelete(index, row) {

        this.$confirm('确定删除此条路由?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.delete(service_prefix + "/route/" + row.no).then(res => {
            const data = res.body;
            if (data.success) {
              this.notifySuccess("删除成功！");
              this.update();
            } else {
              this.notifyError(data.reason);
            }
          });
        }).catch(() => {

        });

      },
      notifySuccess(message) {
        this.$notify({
          title: '操作成功',
          message: message,
          type: 'success'
        });
      },
      notifyError(message) {
        this.$notify.error({
          title: '操作失败',
          message: message
        });
      },
      updateState() {
        this.$http.get(service_prefix + "/flow").then(res => {
          const data = res.body;
          if (data.success) {
            this.flow = data.data;
          }
        });
      },
      updateList() {
        this.routesLoading = true;
        this.$http.get(service_prefix + "/routes").then(res => {
          const data = res.body;
          if (data.success) {
            this.routes = data.data;
            this.routesLoading = false;
          }
        });
      },
      submitForm(formName) {
        if (this.adding) return;
        this.$refs[formName].validate((valid) => {
          if (!valid) return false;
          this.adding = true;
          this.dialogFormVisible = false;
          this.$http.post(service_prefix + "/route", this.route).then(res => {
            const data = res.body;
            if (data.success) {
              this.notifySuccess("添加成功！");
              this.update();
            } else {
              this.notifyError(data.reason);
            }
            this.adding = false;
          });
        });
      }
    }
  }
</script>

